import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Button from '@material-ui/core/Button';
import userStore from '../stores/UserStore';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import SearchIcon from '@material-ui/icons/Search';
import axios from 'axios';
import EditIcon from '@material-ui/icons/Edit';
import DoneIcon from '@material-ui/icons/Done';
import NativeSelect from '@material-ui/core/NativeSelect';
import Avatar from '@material-ui/core/Avatar';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import CancelIcon from '@material-ui/icons/Cancel';
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    userAvatar: {
        marginRight: theme.spacing(2)
    },
    table: {
        minWidth: 650,
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap'
        }
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    formContainer: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: 100
        },
        marginTop: theme.spacing(2)
    },
    disableCell: {
        backgroundColor: '#efefef'
    },
    button: {
        margin: theme.spacing(1)
    },
    holidayCell: {
        color: '#f44336',
        backgroundColor: '#efefef'
    },
    customerField: {
        width: 50
    },
    dateField: {
        width:50
    },
    actions: {
        whiteSpace: 'nowrap',
        '& > *': {
            margin: theme.spacing(0.5)
        }
    }
}));

const dayOfWeek = ['日', '月', '火', '水', '木', '金', '土'];
const kindMap = {
    '0': '',
    '10': '有休', '11': '有休(AM)', '12': '有休(PM)',
    '20': '欠勤', '21': '欠勤(AM)', '22': '欠勤(PM)',
    '30': '特休', '31': '特休(AM)', '32': '特休(PM)',
    '40': '代休', '41': '代休(AM)', '42': '代休(PM)',
    '50': '休出'};

export default function TimeSheet() {
    const classes = useStyles();
    const [user] = useState(userStore.getUser());
    const [year, setYear] = useState(new Date().getFullYear());
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [rows, setRows] = useState([]);
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [oldRows, setOldRows] = useState({});

    const createData = (edit, year, month, date, day, holiday, customer, kind, start_time, end_time, note) => {
        return { edit, year, month, date, day, holiday, customer, kind, start_time, end_time, note };
    };

    /**
     * 検索ボタンがクリックされた場合に呼ばれます。
     */
    const search = () => {
        // 年月の入力がない場合はエラー
        if (!year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!month) {
            setSeverity('error');
            setMessage('月が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(year) < 2020 || Number(year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.get(process.env.REACT_APP_REST_URI + `/times/${year}/${month}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            axios.get(`https://holidays-jp.github.io/api/v1/${year}/date.json`).then((response2) => {
                const records = response.data['records'];
                const japaneseHolidays = response2.data;
                const inputDate = new Date(year, month - 1, 1);
                const rows = [];
                while (true) {
                    const date = inputDate.getDate();
                    const day = inputDate.getDay();
                    // 祝日サービスのキーは'YYYY-MM-dd'形式なので桁数を合わせた祝日キーを生成
                    const holidayKey = `${year}-${('0' + month).slice(-2)}-${('0' + date).slice(-2)}`;
                    // 日曜, 土曜, 祝日の場合は'H'を設定
                    const holiday = (day === 0 || day === 6 || holidayKey in japaneseHolidays) ? 'H' : '';
                    const targetTime = records.find(t => t['date'] === date);
                    if (targetTime) {
                        rows.push(createData(
                            false,
                            year,
                            month,
                            date,
                            dayOfWeek[day],
                            holiday,
                            targetTime['customer'],
                            targetTime['kind'],
                            targetTime['start_time'],
                            targetTime['end_time'],
                            targetTime['note']));
                    }
                    else {
                        rows.push(createData(
                            false,
                            year,
                            month,
                            date,
                            dayOfWeek[day],
                            holiday,
                            '',
                            0,
                            '',
                            '',
                            japaneseHolidays[holidayKey]));
                    }
                    inputDate.setDate(date + 1);
                    if (Number(month) !== (inputDate.getMonth() + 1)) {
                        break;
                    }
                }
                setRows(rows);
                setOldRows({});
            });
        });
    };

    /**
     * 確定ボタンがクリックされた場合に呼ばれます。
     * @param row 勤怠情報
     */
    const handleClickDone = (row) => {
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (row.start_time && !regex.test(row.start_time)) {
            setSeverity('error');
            setMessage('始業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (row.end_time && !regex.test(row.end_time)) {
            setSeverity('error');
            setMessage('終業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }

        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        axios.put(process.env.REACT_APP_REST_URI + `/times/${rowYear}/${rowMonth}/${rowDate}`, {
            customer: row.customer,
            kind: row.kind,
            start_time: row.start_time,
            end_time: row.end_time,
            note: row.note
        }, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            row.edit = false;
            setRows(rows.slice());
            setSeverity('success');
            setMessage('更新しました。');
            setSnackbarOpen(true);
        });
    };

    /**
     * 編集ボタンがクリックされた場合に呼ばれます。
     * @param row 勤怠情報
     */
    const handleClickEdit = (row) => {
        row.edit = true;
        setRows(rows.slice());
    };

    /**
     * キャンセルボタンがクリックされた場合に呼ばれます。
     * @param row 勤怠情報
     */
    const handleClickCancel = (row) => {
        const oldRow = oldRows[String(row.date)];
        if (oldRow) {
            row.customer = oldRow.customer;
            row.kind = oldRow.kind;
            row.start_time = oldRow.start_time;
            row.end_time = oldRow.end_time;
            row.note = oldRow.note;
            delete oldRows[row.date];
            setOldRows(oldRows);
        }
        row.edit = false;
        setRows(rows.slice());
    };

    /**
     * 削除ボタンがクリックされた場合に呼ばれます。
     * @param row 勤怠情報
     */
    const handleClickDelete = (row) => {
        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        axios.delete(process.env.REACT_APP_REST_URI + `/times/${rowYear}/${rowMonth}/${rowDate}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            row.customer = null;
            row.kind = 0;
            row.start_time = null;
            row.end_time = null;
            row.note = null;
            setRows(rows.slice());
            setSeverity('success');
            setMessage('削除しました。');
            setSnackbarOpen(true);
        });
    };

    /**
     * 客先が変更された場合に呼ばれます。
     * @param row 勤怠情報
     * @param customer 客先
     */
    const handleChangeCustomer = (row, customer) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
        row.customer = customer;
        setRows(rows.slice());
    };

    /**
     * 勤休が変更された場合に呼ばれます。
     * @param row 勤怠情報
     * @param kind 勤休
     */
    const handleChangeKind = (row, kind) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
        row.kind = kind;
        setRows(rows.slice());
    };

    /**
     * 始業が変更された場合に呼ばれます。
     * @param row 勤怠情報
     * @param start_time 始業
     */
    const handleChangeStartTime = (row, start_time) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
        row.start_time = start_time;
        setRows(rows.slice());
    };

    /**
     * 終業が変更された場合に呼ばれます。
     * @param row 勤怠情報
     * @param end_time 終業
     */
    const handleChangeEndTime = (row, end_time) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
        row.end_time = end_time;
        setRows(rows.slice());
    };

    /**
     * 備考が変更された場合に呼ばれます。
     * @param row 勤怠情報
     * @param note 備考
     */
    const handleChangeNote = (row, note) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
        row.note = note;
        setRows(rows.slice());
    };

    /**
     * 勤休の選択肢を描画するコンポーネントです。
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const KindOptions = () => {
        const options = [];
        for (let [key, value] of Object.entries(kindMap)) {
            options.push(<option key={key} value={key}>{value}</option>);
        }
        return options;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    return (
        <form className={classes.root}>
            <AppBar position="sticky">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <Button color="inherit">
                        <Avatar alt="avatar" src={user['profile']['image_48']} className={classes.userAvatar} />
                        {user['profile']['real_name']}
                    </Button>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth="md" className={classes.formContainer}>
                <TextField required label="年" defaultValue={year} onChange={(event) => setYear(event.target.value)} />
                <TextField required label="月" defaultValue={month} onChange={(event) => setMonth(event.target.value)} />
                <Button variant="contained" color="primary" size="large" className={classes.button}
                        startIcon={<SearchIcon />} onClick={search}>検索</Button>
            </Container>
            <Container maxWidth="md" className={classes.container}>
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="timesheet" size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center" component="th" scope="row">日付</TableCell>
                                <TableCell align="center" component="th">曜日</TableCell>
                                <TableCell align="center" component="th">休日</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">勤休</TableCell>
                                <TableCell align="center">始業</TableCell>
                                <TableCell align="center">終業</TableCell>
                                <TableCell>備考</TableCell>
                                <TableCell/>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(row => (
                                <TableRow key={row.date}>
                                    <TableCell align="center" className={classes.disableCell}>{row.date}</TableCell>
                                    <TableCell align="center" className={classes.disableCell}>{row.day}</TableCell>
                                    <TableCell align="center" className={classes.holidayCell}>{row.holiday}</TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.customer} className={classes.customerField}
                                                       inputProps={{style: {textAlign: 'center', fontSize: '.9em'}}}
                                                       onChange={(event) => handleChangeCustomer(row, event.target.value)} />
                                        ) : (
                                            <span>{row.customer}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <NativeSelect value={row.kind}
                                                          inputProps={{style: {fontSize: '.9em'}}}
                                                          onChange={(event) => handleChangeKind(row, event.target.value)}>
                                                <KindOptions />
                                            </NativeSelect>
                                        ) : (
                                            <span>{kindMap[row.kind]}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.start_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center', fontSize: '.9em'}}}
                                                       onChange={(event) => handleChangeStartTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.start_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.end_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center', fontSize: '.9em'}}}
                                                       onChange={(event) => handleChangeEndTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.end_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell>
                                        {row.edit ? (
                                            <TextField defaultValue={row.note}
                                                       inputProps={{style: {fontSize: '.9em'}}}
                                                       onChange={(event) => handleChangeNote(row, event.target.value)} />
                                        ) : (
                                            <span>{row.note}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <div className={classes.actions}>
                                                <IconButton aria-label="done" size="small" onClick={() => handleClickDone(row)}>
                                                    <DoneIcon fontSize="inherit" />
                                                </IconButton>
                                                <IconButton aria-label="cancel" size="small" onClick={() => handleClickCancel(row)}>
                                                    <CancelIcon fontSize="inherit" />
                                                </IconButton>
                                            </div>
                                        ) : (
                                            <div className={classes.actions}>
                                                <IconButton aria-label="edit" size="small" onClick={() => handleClickEdit(row)}>
                                                    <EditIcon fontSize="inherit" />
                                                </IconButton>
                                                <IconButton aria-label="delete" size="small" onClick={() => handleClickDelete(row)}>
                                                    <DeleteIcon fontSize="inherit" />
                                                </IconButton>
                                            </div>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}
